.black_bar {
  width: 100%;
  display: flex;
  padding-left: 20px;
  background-color: black;
  color: white;
}

div {
  box-sizing: border-box;
}

.list {
  margin-top: 10px;
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}

.modal {
  padding: 20px;
  background: #e8e8e8;
  text-align: left;
  width: 80%;
  border-radius: 10px;
  margin: auto;
  margin-top: 20px;
  border: 1px solid #5d5d5d;
}

.button_collect {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100px;
  border-bottom: 1px solid gray;
  justify-content: center;
}

.button_collect>button {
  margin-right: 10px;
}

.list_title {
  font-size: 1.2rem;
  font-weight: bolder;
  cursor: pointer;
}

.list_title:hover {
  text-decoration: underline;
}

.modal>button {
  margin-right: 10px;
}

.in_title {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
}

.in_title>button {
  height: 30px;
}

.in_title>input {
  height: 24px;
}

.del_button {
  margin-left: auto;
  margin-right: 10px;
}

.modal>input {
  width: 80%;
  margin: 10px;
}

.modal>textarea {
  width: 80%;
  margin: 10px;
}

.post_button {
  margin-left: 90%;
  margin-top: 20px;
}